import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
// @mui
import { styled, useTheme } from '@mui/material/styles';
import { Box, Card, CardMedia, Stack, Avatar, Typography, CardContent, CardActionArea } from '@mui/material';
// utils
import { fDate } from 'utils/formatTime';//'../../../utils/formatTime';
import { fShortenNumber } from 'utils/formatNumber';
//
import Iconify from 'components/iconify';

// ----------------------------------------------------------------------


// const StyledTitle = styled(Link)({
//   // height: 44,
//   overflow: 'hidden',
//   WebkitLineClamp: 2,
//   display: '-webkit-box',
//   WebkitBoxOrient: 'vertical',
// });



const StyledInfo = styled('div')(({ theme }) => ({
  // height: 60,
  display: 'flex',
  flexWrap: 'wrap',
  justifyContent: 'flex-end',
  overflow: 'hidden',
  marginTop: theme.spacing(1),
  color: theme.palette.text.disabled,
}));


// ----------------------------------------------------------------------

const BlogCard = ({ post, index }) => {
  const { id, title, cover, content, view, comment, share, author, createdAt } = post;
  const theme = useTheme();
  const navigate = useNavigate();
  const latestPostLarge = index === 0;
  const latestPost = index === 1 || index === 2;

  const POST_INFO = [
    // { number: comment, icon: 'eva:message-circle-fill' },
    { number: comment, icon: 'eva:message-circle-fill' },
    { number: view, icon: 'eva:eye-fill' },
    { number: share, icon: 'eva:share-fill' },
  ];

  const handleDetail = (id) => {
    navigate(`/pages/blogs/${id}`)
  }

  return (
    <Card sx={{ width: '100%', maxWidth: 460, position: 'relative' }}>
      <CardActionArea
        onClick={() => handleDetail(id)}
      // onClick={() => handleDetail(id)}
      >
        <CardMedia
          component="img"
          height="260"
          image={cover}
          alt=""
        />
        {/* <StyledCardMedia >
        <StyledCover alt={title} src={cover} />
        </StyledCardMedia> */}
        <CardContent>
          <Typography variant='h5' sx={{ color: theme.palette.text.default }}>
            {title}
          </Typography>
          {/* <StyledTitle
              color="inherit"
              variant="h3"
              underline="hover"
            > */}
          {/* </StyledTitle> */}
          <Typography gutterBottom variant="body1" sx={{ height: 66, color: 'text.secondary', display: 'block', overflow: 'hidden' }}>
            {content.substr(0, 160)}{content.length > 160 ? '...' : ''}
          </Typography>
          <Stack direction="row" spacing={2} mt={2} alignItems={`center`}>
            <Avatar src={author.avatarUrl} />
            <Typography gutterBottom variant="subtitle2" sx={{ color: 'text.secondary', display: 'block' }}>
              {author.name}
            </Typography>
            <Typography gutterBottom variant="subtitle2" sx={{ color: 'text.secondary', display: 'block' }}>
              {fDate(createdAt)}
            </Typography>
          </Stack>
          <StyledInfo>
            {POST_INFO.map((info, index) => (
              <Box
                key={index}
                sx={{
                  display: 'flex',
                  alignItems: 'center',
                  ml: index === 0 ? 0 : 1.5,
                  ...((latestPostLarge || latestPost) && {
                    color: 'grey.500',
                  }),
                }}
              >
                <Iconify icon={info.icon} sx={{ width: 16, height: 16, mr: 0.5 }} />
                <Typography variant="caption">{fShortenNumber(info.number)}</Typography>
              </Box>
            ))}
          </StyledInfo>
        </CardContent>
      </CardActionArea>
    </Card>

  );
}

BlogCard.propTypes = {
  post: PropTypes.object.isRequired,
  index: PropTypes.number,
};

export default BlogCard; 